import './App.css';
import { useState, useEffect } from 'react';
import { BrowserView, MobileView, deviceDetect } from 'react-device-detect';
const internalIp = require('internal-ip');

function App() {
  const [ips, setIp] = useState('')
  const [osPlatform,setOsPlatform] = useState('')
  useEffect(() => {
    async function fetchMyAPI() {
      console.log(await internalIp.v4());
      setIp(await internalIp.v4())
    }
    setOsPlatform(window.navigator.userAgentData.platform)
    fetchMyAPI()
  }, [])
  return (
    <div className="App">
      <h1> IP Address : {ips}</h1>
      <h1> Platform : {osPlatform}</h1>
    </div>
  );
}

export default App;
